* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
}

.background {
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}

.main-container {
  width: 90%;
  height: 90vh;
  margin: 5vh auto 0 auto;
  text-align: center;
  position: relative;
  background-color: #b4b4b4;
}

.bottom {
  width: 100%;
  height: 75vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  bottom: 0;
}

.Door {
  border: solid 2px #cccccc;
  width: 65%;
  height: 100%;
  margin: auto;
}

.floor-sign {
  border: solid 2px #cccccc;
  width: 50%;
  height: 13vh;
  left: 25%;
  top: 1%;
  position: absolute;
}

.screen,
.navigation {
  width: 15%;
  border: solid 2px #cccccc;
  height: 50vh;
  margin: 1rem;
  align-self: flex-start;
}
